<template>
  <div>
    <div class="sort">
      <span :class="{ active: active === 0 }" @click="toggle(0)">全部订单</span>
      <span :class="{ active: active === 1 }" @click="toggle(1)">待付款</span>
      <span :class="{ active: active === 2 }" @click="toggle(2)">待发货</span>
      <span :class="{ active: active === 3 }" @click="toggle(3)">待收货</span>
      <span :class="{ active: active === 4 }" @click="toggle(4)">待评价</span>
      <span :class="{ active: active === 5 }" @click="toggle(5)">已完成</span>
      <span :class="{ active: active === 6 }" @click="toggle(6)">已取消</span>
    </div>
    <div class="order-list">
      <div class="order-item" v-for="item in orderList" :key="item.id">
        <div class="head">
          <span>创建时间：{{ item.createTime }}</span>
          <span>订单编号：{{ item.id }}</span>
        </div>
        <div class="body">
          <div class="column goods">
            <ul>
              <li v-for="subitem in item.skus" :key="subitem.id">
                <a class="image" href="javascript:;">
                  <el-image :src="subitem.image" lazy></el-image>
                </a>
                <div class="info">
                  <p class="name ellipsis-2">
                    {{ subitem.name }}
                  </p>
                  <p class="attr ellipsis">
                    <span>{{ subitem.attrsText }}</span>
                  </p>
                </div>
                <div class="price">&yen;{{ subitem.realPay }}</div>
                <div class="count">x{{ subitem.quantity }}</div>
              </li>
            </ul>
          </div>
          <div class="column state">
            <p>{{ item.orderState }}</p>
          </div>
          <div class="column amount">
            <p class="red">&yen;{{ item.payMoney }}</p>
          </div>
          <div class="column action">
            <p><a href="javascript:;">查看详情</a></p>
            <!-- <p v-if="item.orderState === 1">
              <a href="javascript:;">取消订单</a>
            </p>
            <p v-if="[2, 3, 4, 5].includes(item.orderState)">
              <a href="javascript:;">再次购买</a>
            </p>
            <p v-if="[4, 5].includes(item.orderState)">
              <a href="javascript:;">申请售后</a>
            </p> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderList: {},
      queryInfo: {
        page: 1,
        pageSize: 10,
        orderState: 0,
      },
      active: 0,
    };
  },
  methods: {
    async getOrderList() {
      const { data } = await this.$axios.get("/member/order", {
        params: this.queryInfo,
      });
      this.orderList = data.result.items;
    },
    toggle(index) {
      this.active = index;
      this.queryInfo.orderState = index;
      this.getOrderList();
    },
  },
  created() {
    this.getOrderList();
  },
};
</script>

<style lang="less" scoped>
div {
  .sort {
    background: #ffffff;
    display: flex;
    span {
      padding: 15px 25px;
      font-size: 16px;
      border: 1px solid #dcdfe6;
      border-left: none;
      &:hover {
        cursor: pointer;
      }
      &.active {
        border-top: 2px solid #409eff;
        border-bottom: none;
      }
    }
  }
  .order-list {
    background: #ffffff;
    padding: 20px;
    .order-item {
      margin-bottom: 20px;
      border: 1px solid #dcdfe6;
      .head {
        height: 50px;
        line-height: 50px;
        background: #dcdfe6;
        padding: 0 20px;
        overflow: hidden;
        span {
          margin-right: 20px;
        }
      }
      .body {
        display: flex;
        align-items: stretch;
        .column {
          border-left: 1px solid #dcdfe6;
          text-align: center;
          padding: 20px;
          > p {
            padding-top: 10px;
          }
          &:first-child {
            border-left: none;
          }
          &.goods {
            flex: 1;
            padding: 0;
            align-self: center;
            ul {
              li {
                border-bottom: 1px solid #dcdfe6;
                padding: 10px;
                display: flex;
                &:last-child {
                  border-bottom: none;
                }
                .image {
                  width: 70px;
                  height: 70px;
                  border: 1px solid #dcdfe6;
                }
                .info {
                  width: 220px;
                  text-align: left;
                  padding: 0 10px;
                  p {
                    margin-bottom: 5px;
                    &.name {
                      height: 38px;
                    }
                    &.attr {
                      color: #909399;
                      font-size: 12px;
                      span {
                        margin-right: 5px;
                      }
                    }
                  }
                }
                .price {
                  width: 100px;
                }
                .count {
                  width: 80px;
                }
              }
            }
          }
          &.state {
            width: 120px;
          }
          &.amount {
            width: 200px;
            .red {
              color: #cf4444;
            }
          }
          &.action {
            width: 140px;
            a {
              display: block;
              &:hover {
                color: #409eff;
              }
            }
          }
        }
      }
    }
  }
}
</style>